import { ELEMENTS_TYPE } from '../Constants';
import { SURVEYS_ERROR_MESSAGE } from '../Messages';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import { isValidPhoneNumber } from 'react-phone-number-input';
import { convertPhoneValue } from '../../../helpers/number';

export const onValidateHandler = (survey, options) => {
  const question = _get(options, 'question');
  if (question.getType() === ELEMENTS_TYPE.phoneInput) {
    const isRequired = _get(question, 'isRequired', false);
    phoneNumberValidator(options, isRequired);
  }
};

const phoneNumberValidator = (options, isRequired) => {
  const phoneNumberValue = _get(options, 'value', '');

  if (!phoneNumberValue && !isRequired) return;

  if (!isValidPhoneNumber(convertPhoneValue(phoneNumberValue)))
    options.error = SURVEYS_ERROR_MESSAGE.invalidPhoneNumber;
};

const getAcceptedTypes = question => {
  const { acceptedTypes } = question;
  return acceptedTypes
    ? acceptedTypes.split(',').map(type => type.replace('.', '').trim())
    : [];
};

export const getUploadFileErrorMessage = fileTypes => {
  const types = fileTypes.map(type => `.${type}`);
  const [firstType, ...restTypes] = types;

  return `We accept file uploads in ${restTypes.join(
    ', ',
  )} or ${firstType} formats`;
};

export const getValidUploadFiles = options => {
  const { files, callback, question } = options;
  if (_isEmpty(files)) return [];
  const acceptedTypes = getAcceptedTypes(question);
  if (_isEmpty(acceptedTypes)) return files;
  const validFiles = files.filter(file => {
    const type = file.type.split('/')[1];
    return acceptedTypes.includes(type);
  });
  const isInvalid = validFiles.length !== files.length;
  if (isInvalid) {
    const error = getUploadFileErrorMessage(acceptedTypes);
    question.addError(error);
    callback('error');
  }
  return validFiles;
};
