import _isNumber from 'lodash/isNumber';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import { GET_DYNAMIC_VALUES_PARAMS, TITLE_FEES } from './Constants';
import { hasOtherValue, removeOtherValue } from '../../../helpers/Offer';
export { getMonetaryTotalAmount, triggerMonetaryData } from './SCARForm';

export function rounder(params) {
  const multiply = params.reduce((accumulator, current) => {
    return accumulator * (_isNumber(current) ? current : 1);
  }, 1);
  return Math.round(multiply);
}

export function getTitleFee(listPrice) {
  const titleFeeItem = TITLE_FEES.filter(
    item => item.ListPrice <= listPrice,
  ).sort(
    (item1, item2) => _get(item2, 'ListPrice', 0) - _get(item1, 'ListPrice', 0),
  );
  if (_isEmpty(titleFeeItem)) return 0;
  return _get(titleFeeItem, '0.TitleFee', 0);
}

// Usage: Additional_Buyer_Name expression in SF form
export function getDynamicValues(params) {
  if (_get(params, 'length', 0) < GET_DYNAMIC_VALUES_PARAMS) return '';
  const [parentName, childName] = params;

  const parentField = this.survey.getQuestionByName(parentName);
  if (_isEmpty(parentField.value)) return '';
  return parentField.value.map(value => _get(value, childName, '')).join(', ');
}

// Usage: summary_personal_item_requests expression in OS form
export function getCheckboxWithOtherValue(params) {
  if (_get(params, 'length', 0) < 1) return '';
  const [questionName] = params;
  const otherQuestionName = `${questionName}_other`;

  const question = this.survey.getQuestionByName(questionName);
  const { value } = question;
  if (hasOtherValue(value)) {
    const otherValue = this.survey.getQuestionByName(otherQuestionName).value;
    const restValues = removeOtherValue(value).join(', ');
    return `${restValues ? `${restValues}, ` : ''}${otherValue || ''}`;
  }
  return value.join(', ');
}

const rangeTotalOrder = {
  1: '1 ~ 3 offers',
  2: '1 ~ 3 offers',
  3: '1 ~ 3 offers',
  4: '4 ~ 6 offers',
  5: '4 ~ 6 offers',
  6: '4 ~ 6 offers',
  7: '7 ~ 9 offers',
  8: '7 ~ 9 offers',
  9: '7 ~ 9 offers',
};

// Usage: getTotalOffer expression in SB form
export function getTotalOffer(params) {
  if (_get(params, 'length', 0) < 1) return '';
  const [fieldName] = params;
  const field = this.survey.getQuestionByName(fieldName);
  const value = _get(field, 'value', '');
  if (!value || value.length === '') return '';
  return _get(rangeTotalOrder, value, 'Over 10 offers');
}
